.slider-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  border-radius: 15px;
  overflow: hidden;
  max-height: 400px;
}

/* 슬라이드 아이템 */
.slider-item {
  position: relative;
  border-radius: 15px;
}

.slick-list {
  overflow: hidden;
}

.slick-slide {
  display: flex;
  justify-content: center;
  border-radius: 15px;
}

.slick-slide img {
  width: 100%;
  max-width: 1200px;
  border-radius: 15px;
  max-height: 400px;
  min-height: 300px;
}

.slick-prev,
.slick-next {
  z-index: 1;
  font-size: 0;
  color: transparent; /* 텍스트 색상 투명하게 */
  background-color: transparent; /* 배경색 투명하게 */
  border: none; /* 테두리 없애기 */
  outline: none;
  width: 30px;
  height: 30px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.slick-prev {
  left: 20px;
}

.slick-next {
  right: 20px;
}

.slick-dots {
  bottom: 10px;
  display: flex;
  justify-content: center;
}
