.search-bar {
  width: 100%;
  max-width: var(--container-max-width); /* Use a variable for max width */
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px 15px; /* 패딩을 줄여서 크기를 좀 더 작게 */
  box-sizing: border-box;
  gap: 10px; /* Add space between input and buttons */
}

/* 검색 입력창 */
.search-bar .serchbarInput {
  width: 65%; /* Adjusted width */
  height: 36px;
  border: 2px solid var(--border-color); /* 경계선 두께를 1px로 수정 */
  border-radius: var(--border-radius); /* Use border radius variable */
  padding: 0 10px; /* 좌우 패딩 추가 */
  font-size: var(--font-size-base);
  color: var(--text-color); /* Text color */
  transition: border-color var(--transition-speed);
}

.serchbarInput:focus {
  border: 2px solid var(--primary-color-hover); /* Focus color aligned with buttons */
}

/* 버튼 공통 스타일 */
.search-bar button {
  height: 36px;
  background-color: var(--primary-color); /* Use secondary color */
  color: var(--background-color-light); /* Light text color */
  border: none;
  border-radius: var(--border-radius); /* Rounded corners */
  font-size: 14px;
  cursor: pointer;
  transition: background-color var(--transition-speed);
  padding: 0 15px; /* Consistent padding */
}

/* 검색 버튼 hover 상태 */
.search-bar .search-button:hover {
  background-color: var(--primary-color-hover); /* Use hover color */
}

/* 버튼 여백 */
.search-bar .reset-button {
  background-color: var(--error-color);
  margin-left: 10px; /* Space between buttons */
}

/* 검색 버튼 active 상태 */
.search-bar .search-button:active {
  transform: translateY(2px); /* Click effect */
}

/* 초기화 버튼 hover 상태 */
.search-bar .reset-button:hover {
  background-color: #c62828; /* Slightly lighter error hover color */
}

/* 에러 메시지 */
.error-message {
  color: var(--error-color); /* Use error color variable */
  font-size: 14px;
  margin-top: 10px;
  text-align: center;
}
